import React, { useState } from "react";
import Fab from "@mui/material/Fab";

function ProjectSvg(props) {
  const [isIn, setIsIN] = useState(false);

  return (
    <div onMouseOver={() => setIsIN(true)} onMouseOut={() => setIsIN(false)}>
      <Fab id="pb">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          width="20"
          height="20"
          fill={isIn ? "rgb(38, 201, 38)" : "rgb(100,100,100)"}
          class="bi bi-github"
          viewBox="0 0 16 16"
        >
          <path d={props.path} />
        </svg>
      </Fab>
    </div>
  );
}

export default ProjectSvg;
