import React, { useState } from "react";

function Card(props) {
  const [mouseIn, setMouseIn] = useState(false);

  function handleMouseIn() {
    setMouseIn(true);
  }

  function handleMouseOut() {
    setMouseIn(false);
  }

  return (
    <div
      onMouseEnter={handleMouseIn}
      onMouseLeave={handleMouseOut}
      style={{
        background:
          mouseIn &&
          "linear-gradient(180deg, rgba(0,0,0,1) 30%, rgba(53,186,20,0.5) 100%)"
      }}
      className="box"
    >
      <div id="card">
        <div id="card-img-box">
          <img src={props.img} alt="logo" className="card-img" />
        </div>
        <h1 className="card-info card-title">{props.heading}</h1>
        <p className="card-info card-p">
          {mouseIn ? props.info : props.info.substring(0, 150) + "..."}
        </p>
      </div>
    </div>
  );
}

export default Card;
