import React, { useState } from "react";
import Title from "./Title";

function Contact() {
  const [isIn, setIsIN] = useState(false);

  return (
    <section id="contact">
      <Title title="Contact" />
      <div id="contact-box">
        <form action="submit">
          <div className="inputs">
            <input type="text" placeholder=" First Name" />
            <input type="text" placeholder=" Last Name" />
            <input type="email" placeholder=" Email" id="mail" />
            <textarea rows="7" placeholder="Message" id="message" />
          </div>
          <div id="button">
            <button
              onMouseOver={() => setIsIN(true)}
              onMouseOut={() => setIsIN(false)}
              style={{ backgroundColor: isIn && " rgb(38, 201, 38)" }}
            >
              Submit
            </button>
          </div>
        </form>
      </div>
    </section>
  );
}

export default Contact;
