import React, { useState } from "react";
import { Link } from "react-scroll";

function Tab(props) {
  const [isIn, setIsIN] = useState(false);
  const [isActive, setActive] = useState(false);

  return (
    <Link
      className={isActive && "active"}
      to={props.tabName.toLowerCase()}
      spy={true}
      smooth={true}
      offset={props.tabName === "Contact" ? -100 : -150}
      duration={500}
      onMouseOver={() => setIsIN(true)}
      onMouseOut={() => setIsIN(false)}
      onClick={() => setActive(true)}
      style={{ color: isIn && "rgb(151, 151, 151)" }}
    >
      {props.tabName}
    </Link>
  );
}

export default Tab;
